<template>
  <div class="appointment">
    <mt-header>
      <a @click="goBack" slot="left">
        <mt-button icon="back">预约确认</mt-button>
      </a>
    </mt-header>
    <div class="appointmentBody">
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">被护理人</span>
          </div>
          <div class="w_cell_value">
            <span>{{beCaredDetail.name}}</span> 
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">服务类型</span>
          </div>
          <div class="w_cell_value">
            <span>{{glodalVal.jrOrJx}}</span> 
          </div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">陪护分类</span>
          </div>
          <div class="w_cell_value">
            <span>{{glodalVal.companyType}}</span> 
          </div>
        </div>
      </a>
      <a class="w_cell" v-if="glodalVal.jrOrJx == '计日类'">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">服务时间</span>
          </div>
          <div class="w_cell_value">
            <span @click="timeShow = true">{{time}}</span> 
          </div>
        </div>
      </a>
      <a class="w_cell" v-else>
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">服务时间</span>
          </div>
          <div class="w_cell_value" style="flex-wrap:wrap;">
            <ul style="width:100%; margin-left:2%;margin-bottom:0.1rem;">
              <li v-for="t in JXtime">{{t}}<span style="margin-left:0.2rem;font-size:0.5rem;color:#d06091;" @click="deleteOneJxTime(t)">-</span></li>
            </ul>
            <span @click="timeShow = true">{{time}}</span> 
          </div>
        </div>
      </a>
      <a class="w_cell" v-if="type == 'ys'">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">选择门店</span>
          </div>
          <div class="w_cell_value">
            <span @click="storeChange">{{store}}</span> 
          </div>
        </div>
      </a>
      <a class="w_cell" v-if="glodalVal.jrOrJx == '计项类'">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">服务时长</span>
          </div>
          <div class="w_cell_value">
            <span>{{duration}}</span> 
          </div>
        </div>
      </a>
      <a class="w_cell" v-if="type == 'ys' || glodalVal.home == true">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">服务地址</span>
          </div>
          <div class="w_cell_value">
            <span><input class="w_core" type="text" placeholder="填写家庭地址" v-model="address"></span>
          </div>
        </div>
      </a>
      <div v-else>
        <a class="w_cell">
          <div class="w_cell_wraper">
            <div class="w_cell_title">
              <span class="w_cell_text">服务地址</span>
            </div>
            <div class="w_cell_value">
              <span @click=" hospitalShow = true">{{hospital}}</span> 
            </div>
          </div>
        </a>
        <a class="w_cell">
          <div class="w_cell_wraper">
            <div class="w_cell_title">
              <span class="w_cell_text"></span>
            </div>
            <div class="w_cell_value">
              <span @click="departmentsChange">{{departments}}</span> 
              <span><input class="w_core" type="text" placeholder="填写病区床号" v-model="region"></span>
            </div>
          </div>
        </a>
      </div>
      <a class="w_cell" style="min-height:1rem;">
        <div class="w_cell_wraper">
          <div class="w_cell_title">
            <span class="w_cell_text">补充说明</span>
          </div>
          <div class="w_cell_value" style="border:none;"></div>
        </div>
      </a>
      <a class="w_cell">
        <div class="w_cell_wraper">
          <textarea v-model="remark" class="w_field_core" placeholder=""  rows="5"></textarea>
        </div>
      </a>
     <mt-actionsheet :actions="oneMonthDayActions" v-model="timeShow" cancel-text=""></mt-actionsheet>
     <mt-actionsheet :actions="onDayTimeActions" v-model="dayTimeShow" cancel-text=""></mt-actionsheet>
     <mt-actionsheet :actions="ondurationDayActions" v-model="durationDayShow" cancel-text=""></mt-actionsheet>
     <mt-actionsheet :actions="hospitalActions" v-model="hospitalShow" cancel-text=""></mt-actionsheet>
     <mt-actionsheet :actions="departmentsActions" v-model="departmentsShow" cancel-text=""></mt-actionsheet>
     <mt-actionsheet :actions="storesActions" v-model="storesShow" cancel-text=""></mt-actionsheet>
    </div>
    <div class="footer">
      <p class="price">
        ¥ {{price}}
      </p>
      <mt-button @click.native="handleAppoitment" size="small">立即预约</mt-button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions} from 'vuex'
import {oneMonthTime, timeArr, dayArr,timeJXArr} from '../utils/oneMonthTime'
import {addzero} from '../utils/filters'
import { Toast } from 'mint-ui';
export default {
  name: 'appointment',
  data () {
    return {
      id: '',
      hospitalShow: false,
      onDayTimeActions: [],
      storesActions: [],
      storeId: null,
      storesShow: false,
      departmentsId: null,
      hospitalActions: [],
      departmentsActions: [],
      durationDayShow: false,
      departmentsShow: false,
      type: '',
      duration: null,
      time: '点击选择服务时间',
      hospital: '选择所在医院',
      departments: '所在科室',
      timeShow: false,
      dayTimeShow: false,
      testDay:'',
      testTime:'',
      store: '点击选择门店',
      hospitalId: null,
      // showDay: false,
      showHospital: false,
      //showDepartments: false,
      //showStore: false,
      region: '',
      durationDay: '',
      durationUnit: 3,
      oneMonthTimeAction: [],
      JXtime:[],
      duration: '',
      hospitalEnter: ''
    }
  },
  watch: {
    day: function(val, oldVal) {
      if(val != oldVal) {
        conosl.log(this.durationDay);
        this.price = this.durationDay * this.glodalVal.companyType.split(' ')[1];
      };
    },
    testDay: function(val, oldVal) {
      const ctx = this;
      if(val != oldVal) {
         let onDatTimeActions = [{name: '请选择时间', method: function(){}}];
          const year = val.slice(0,4);
          const month = val.slice(5,7);
          const day = val.slice(8,10);
        
          const str = `${year}/${month}/${day}`;
          const nowYear = new Date().getFullYear();
          const nowMonth = new Date().getMonth() + 1;
          const nowDay = new Date().getDate();
          const newStr = `${nowYear}/${nowMonth}/${nowDay}`;
          if(new Date(str) > new Date(newStr)) {
            for(let i = 1; i <=24; i++) {
              if(ctx.glodalVal.jrOrJx == '计项类') {
                let obj = {};
                let str = addzero(i) + ':' + '00';
                obj['name'] = str;
                obj['method'] = function() {
                  if(ctx.JXtime.indexOf(`${ctx.testDay} ${str}`) == -1)  {
                    ctx.JXtime.push(`${ctx.testDay} ${str}`);
                    ctx.durationUnit = 5;
                  }
                }
                onDatTimeActions.push(obj);
                let newobj = {};
                let newstr = addzero(i) + ':' + '30';
                newobj['name'] = newstr;
                newobj['method'] = function() {
                  if(ctx.JXtime.indexOf(`${ctx.testDay} ${str}`) == -1)  {
                    ctx.JXtime.push(`${ctx.testDay} ${str}`);
                    ctx.durationUnit = 5;
                  }
                }
                onDatTimeActions.push(newobj);
              } else {
                for(let i = 1; i <=24; i++) {
                  let obj = {};
                  let str = addzero(i) + ':' + '00';
                  obj['name'] = str;
                  obj['method'] = function() {
                    ctx.testTime = str;
                    ctx.time = `${ctx.testDay} ${str}`;
                    if(ctx.duration < 1) {
                      ctx.durationDay = 0.5
                    } else if(ctx.type == 'ys'){
                      ctx.durationDayShow = false;
                    } else{
                      ctx.durationDayShow = true;
                    }
                  }
                  onDatTimeActions.push(obj);
                }
              }
            }
          } else {
            for(let i = new Date().getHours()+1; i <=24; i++) {
              if(ctx.glodalVal.jrOrJx == '计项类') {
                let obj = {};
                let str = addzero(i) + ':' + '00';
                obj['name'] = str;
                obj['method'] = function() {
                  if(ctx.JXtime.indexOf(`${ctx.testDay} ${str}`) == -1)  {
                    ctx.JXtime.push(`${ctx.testDay} ${str}`);
                    ctx.durationUnit = 5;
                  }
                }
                onDatTimeActions.push(obj);
                let newobj = {};
                let newstr = addzero(i) + ':' + '30';
                newobj['name'] = newstr;
                newobj['method'] = function() {
                  if(ctx.JXtime.indexOf(`${ctx.testDay} ${str}`) == -1)  {
                    ctx.JXtime.push(`${ctx.testDay} ${str}`);
                    ctx.durationUnit = 5;
                  }
                }
                onDatTimeActions.push(newobj);
              } else {
                for(let i = 1; i <=24; i++) {
                  let obj = {};
                  let str = addzero(i) + ':' + '00';
                  obj['name'] = str;
                  obj['method'] = function() {
                    ctx.testTime = str;
                    ctx.time = `${ctx.testDay} ${str}`;
                    if(ctx.duration < 1) {
                      ctx.durationDay = 0.5
                    } else if(ctx.type == 'ys'){
                      ctx.durationDayShow = false;
                    } else {
                      ctx.durationDayShow = true;
                    }
                  }
                  onDatTimeActions.push(obj);
                }
              }
            }
          }
          this.onDayTimeActions = onDatTimeActions;
      }
    }
  },
  created() {
    const ctx = this;
    if(this.hospitals.items.length >= 0){
      this.hospitals.items.forEach(i => {
        let obj = {};
        obj['name'] = i.name;
        obj['method'] = function() {
          ctx.hospital = i.name;
          ctx.hospitalId = i.id;
          ctx.hospitalEnter = i.entered;
          ctx.fetchDepartments(i.id).catch(error => {
            if(error.body.code == 3001) {
              this.$router.push('/');
            }
          });
        }
        this.hospitalActions.push(obj);
      })
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
        vm.type = to.query.type;
      
        vm.duration = to.query.duration;
        vm.getBecaredDetail(vm.glodalVal.beCaredId).catch(error => {
          if(error.body.code == 3001) {
            this.$router.push('/');
          }
        });
        vm.id = to.query.id;
        if(to.query.type == 'ys') {
          vm.getZoneStore(vm.glodalVal.currentCity)
        }
    })
  },
  computed: {
    ...mapGetters({
      beCaredDetail: 'getBeCaredDetail',
      glodalVal: 'getGlodalVal',
      hospitals: 'getHospitals',
      allDepartments: 'getDepartments',
      citiesProvince: 'getCities',
      myServices: 'getMyServices'
    }),
    price: function() {
      if(this.type == 'ys' || this.duration == 0.5) {
        return this.glodalVal.companyType.match(/\d+元/)[0].match(/\d+/)[0];
      } else if(this.type == 'jx') {
        return this.glodalVal.companyType.match(/\d+元/)[0].match(/\d+/)[0] * this.JXtime.length;
      } else {
        return (this.durationDay || 0) * parseInt(this.glodalVal.companyType.split(' ')[1]);
      }
      
    },
    ondurationDayActions: function() {
      const ctx = this;
      let ondurationDayActions = [{name:'请选择服务天数',method: function(){}}];
      for(let i = 1; i <= 60 ; i++) {
        let obj = {};
        obj['name'] = i;
        obj['method'] = function() {
          ctx.time = `${ctx.testDay} ${ctx.testTime} ${i}天`;
          ctx.durationDay = i;
        }
        ondurationDayActions.push(obj);
      }
      return ondurationDayActions;
    },
    oneMonthDayActions: function() {
      const ctx = this;
      let oneMonthTimeAction = [{name:'请选择日期',method: function(){}}];
      for(let i = 0; i < 31; i++) {
        let obj = {};
        const now = Date.now();
        const nextMonth = new Date(now + i * 83520000).getMonth() + 1;
        const nextYear = new Date(now + i * 83520000).getFullYear();
        const nextDay = new Date(now + i * 83520000).getDate();
        const str = nextYear + '.' + addzero(nextMonth) + '.' + addzero(nextDay);
        obj['name'] = str;
        obj['method'] = function() {
          ctx.testDay = str;
          ctx.dayTimeShow = true;
        }
        oneMonthTimeAction.push(obj);
      }
      return oneMonthTimeAction;
    },
  },
  methods: {
    ...mapActions({
      getBecaredDetail: 'getBecaredDetail',
      fetchDepartments: 'fetchDepartments',
      addAppointment: 'addAppointment',
      addMyAppointment: 'addMyAppointment',
      addJXAppoinment: 'addJXAppoinment',
      getZoneStore: 'getZoneStore',
      addYSAppoinment: 'addYSAppoinment',
      changeTabSelected: 'changeTabSelected'
    }),
    goBack: function() {
      history.go(-1);
    },
    deleteOneJxTime: function(t) {
      const index = this.JXtime.indexOf(t);
      if(index >= 0) {
        for(let i = i; i < this.JXtime.length - 1; i++) {
          this.JXtime[i] = this.JXtime[i+1];
        }
        this.JXtime.pop();
      }
    },
    departmentsChange: function() {
      const ctx = this;
    
      this.allDepartments.forEach(i => {
        if(!ctx.hospitalEnter) {
            this.departmentsActions = [];
            let obj = {};
            obj.name = i.name;
            obj['method'] = function() {
              ctx.departments = i.name;
              ctx.departmentsId = i.id;
            }
            this.departmentsActions.push(obj);
        } else {
          if(i.businessSupervisorId && i.operationSupervisorId) {
            this.departmentsActions = [];
            let obj = {};
            obj.name = i.name;
            obj['method'] = function() {
              ctx.departments = i.name;
              ctx.departmentsId = i.id;
            }
            this.departmentsActions.push(obj);
          }
        }
      })
      this.departmentsShow = true;
    },
    storeChange: function() {
      const ctx = this;
      this.glodalVal.store.forEach(i => {
        let obj = {};
        obj.name = i.name;
        obj.method = function() {
          ctx.store = i.name;
          ctx.storeId = i.id;
        }
        this.storesActions.push(obj);
      })
      this.storesShow = true;
    },
      handleAppoitment: function() {
      
          let province = '';
          const cityProvince = this.citiesProvince;
          cityProvince.forEach(i => {
            if(i.city == this.glodalVal.currentCity) {
              province = i.province;
            }
          })

          let obj = {};
          obj.beCareId = this.glodalVal.beCaredId;
          
          obj.remark = this.remark;
          obj.city = this.glodalVal.currentCity;
          obj.province = province;
          obj.serviceDate = this.testDay.replace(/\./g,'-') + ' ' + this.testTime;
          obj.serviceDurationUnit = this.durationUnit;
          obj.serviceDuration = this.durationDay;
        if(this.type == 'my') {
          obj.infantMom = this.glodalVal.infantMom;
          obj.infantMom.serviceId = this.id;
          obj.hospitalId = this.hospitalId;
          obj.departmentId = this.departmentsId;
          obj.region = this.region;
          this.addMyAppointment(obj).then(res => {
            if(res.ok) {
              this.changeTabSelected('订单')
              this.$router.push({path:'/index', query : { 'back': 'order' }});
            }
          }, error => {
            if(error.body.code == 3001) {
              this.$router.push('/login');
            }
          })
        } else if(this.type == 'jr') {
          if(this.glodalVal.home) {
            obj.address = this.address;
          } else {
            obj.hospitalId = this.hospitalId;
            obj.departmentId = this.departmentsId;
            obj.region = this.region;
          }
          obj.diseaseCare = {
            "serviceId": this.id,
            "selfCareAbility": this.glodalVal.selfCareAbility,
            "illnessLevel": this.glodalVal.illnessLevel
          }
          this.addAppointment(obj).then(res => {
            if(res.ok) {
              this.changeTabSelected('订单')
              this.$router.push({path:'/index', query : { 'back': 'order' }});
            }
          },error => {
            if(error.body.code == 3001) {
              this.$router.push('/login');
            }
          });
        } else if(this.type == 'jx') {
          obj.hospitalId = this.hospitalId;
          obj.departmentId = this.departmentsId;
          obj.region = this.region;
          obj.serviceDates = this.JXtime;
          obj.serviceDuration = this.duration;
          obj.itemClass = {
            serviceIndex: this.glodalVal.serviceIndex,
            serviceId: this.id
          }
          this.addJXAppoinment(obj).then(res => {
            if(res.ok) {
              this.changeTabSelected('订单')
              this.$router.push({path:'/index', query : { 'back': 'order' }});
            }
          }, error => {
            if(error.body.code == 3001) {
              this.$router.push('/login');
            }
          });
        } else if(this.type == 'ys') {
          obj.address = this.address;
          obj.serviceDuration = this.glodalVal.companyType.match(/\/(\d+)/)[1];
          obj.infantMom = this.glodalVal.infantMom;
          obj.infantMom.serviceId = this.id;
          this.glodalVal.store.forEach(i => {
            if(i.name == this.store) {
               obj.storeId = i.id;
            }
          })
          this.addYSAppoinment(obj).then(res => {
            if(res.ok) {
              this.changeTabSelected('订单')
              this.$router.push({path:'/index', query : { 'back': 'order' }});
            }
          }, error => {
            if(error.body.code == 3001) {
              this.$router.push('/login');
            }
          });
        }
      }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.appointment {
  font-size: 0.4rem;
}
.appointment .mint-cell-value {
  flex: 4;
}
.appointment .mint-popup {
  width: 65%;
}
.appointment .footer{
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    z-index: 1;
    width: 100%;
    background-color: #fff;
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #d06091;
}
.appointment .footer .price {
  height: 1rem;
  margin-left: 0.5rem;
  line-height: 1rem;
  color: #ff8f0b;
}
.appointment .footer .mint-button {
  background-color: #d06091;
  color: #fff;
}
.appointment .mint-actionsheet {
  max-height: 11.5rem;
  overflow: scroll;
}
.appointment .mint-actionsheet-listitem{
      font-size: 0.5rem;
    line-height: 1.2rem;
    height: 1.2rem;
}
li {
  list-style: none;
}
/*w_popup*/

</style>



